* , *:before , *:after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html , body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
}

img , picture , video , canvas , svg {
  display: block;
  max-width: 100%;
}

input , button , textarea , select {
  font: inherit;
}

p , h1 , h2 , h3 , h4 , h5 , h6 {
  overflow-wrap: break-word;
}

#root , #__next {
  isolation: isolate;
}
